.loader-main {
  position: relative;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-gap: 2px;
  width: 100px;
  height: 100px; }
  .loader-main > div {
    position: relative;
    width: 100%;
    height: 100%;
    background: tomato;
    transform: scale(0);
    transform-origin: center center;
    animation: loader 1.3s infinite linear; }
    .loader-main > div:nth-of-type(1), .loader-main > div:nth-of-type(5), .loader-main > div:nth-of-type(9) {
      animation-delay: 0.4s; }
    .loader-main > div:nth-of-type(4), .loader-main > div:nth-of-type(8) {
      animation-delay: 0.2s; }
    .loader-main > div:nth-of-type(2), .loader-main > div:nth-of-type(6) {
      animation-delay: 0.6s; }
    .loader-main > div:nth-of-type(3) {
      animation-delay: 0.8s; }

@keyframes loader {
  0% {
    transform: scale(0); }
  40% {
    transform: scale(1); }
  80% {
    transform: scale(1); }
  100% {
    transform: scale(0); } }

