    position: relative;
    background: none;
    margin-right: 15px;
    vertical-align: top;
    cursor: pointer; }
    .filter_box .checkbox_label .checkbox:hover:before {
      border-color: #f50808; }
    .filter_box .checkbox_label .checkbox:before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      background-color: #fff;
      border: 1px solid #858282; }
    .filter_box .checkbox_label .checkbox:after {
