      position: absolute;
      opacity: 0;
      width: 7px;
      height: 13px;
      transition: 0.2s ease-in;
      border-right: 3px solid #fff;
      border-bottom: 3px solid #fff;
      right: 6px;
      top: 1px; }
    .filter_box .checkbox_label .checkbox:checked::before {
